import { Component, OnInit, ChangeDetectionStrategy, Input } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { RestApiService } from "../../shared/rest-api.service";
import { AuthService } from 'angularx-social-login';
import { SocialUser } from 'angularx-social-login';
import { GoogleLoginProvider, FacebookLoginProvider } from 'angularx-social-login';
import { authLogin, AppState } from '../../core/core.module';
import { Store, select } from '@ngrx/store';
import { Router } from '@angular/router';

@Component({
  selector: 'shaale-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class LoginComponent implements OnInit {

  loginForm: FormGroup;

  hide = true;

  user: SocialUser;

  constructor(
    private formBuilder: FormBuilder,
    public restApi: RestApiService,
    private authService: AuthService,
    private store: Store<AppState>,
    private router: Router
  ) { }

  ngOnInit() {

    this.loginForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(6), Validators.pattern('^[a-zA-Z0-9]*$')]]
    });

    this.authService.authState.subscribe((user) => {
      this.user = user;
      console.log(user);
    });
  }

  public hasError = (controlName: string, errorName: string) => {
    return this.loginForm.controls[controlName].hasError(errorName);
  }

  signInWithGoogle(): void {
    this.authService.signIn(GoogleLoginProvider.PROVIDER_ID)
      .then((response) => {
        console.log(response);
        let socialUser = {
          provider: response.provider.toLowerCase(),
          token: response.idToken,
        }
        console.log(socialUser);
        this.submitSocialDetails(socialUser);
      })
      .catch(err => alert(err))
  }

  signInWithFB(): void {
    this.authService.signIn(FacebookLoginProvider.PROVIDER_ID)
      .then((response) => {
        console.log(response);
        let socialUser = {
          provider: response.provider.toLowerCase(),
          token: response.authToken,
        }
        console.log(socialUser);
        this.submitSocialDetails(socialUser);
      })
      .catch(err => alert(err))
  }


  // login form submit
  onSubmit() {
    console.log(this.loginForm.value);
    this.restApi.loginUser(this.loginForm.value)
      .subscribe(
        (onSuccess: any) => {
          console.log(onSuccess);
          localStorage.setItem("token", onSuccess.token);
          this.getUserInfo();
        },
        (onError: any) => {
          console.log(onError);
        }
      );
  }

  // social login/signup
  submitSocialDetails(socialData) {
    this.restApi.loginUserSocial(socialData)
    .subscribe(
      (onSuccess: any) => {
        console.log(onSuccess);
        localStorage.setItem("token", onSuccess.token);
        this.getUserInfo();
      },
      (onError: any) => {
        console.log(onError);
      }
    );
  }

  // Get user details
  getUserInfo() {
    this.restApi.getUserDetails()
    .subscribe(
      (onSuccess: any) => {
        console.log(onSuccess);
        if (onSuccess.isPhoneVerified == false) {
          if ((onSuccess.phone == "") || (onSuccess.phone == null)) {
            console.log(onSuccess.phone != "", onSuccess.phone != null);
            this.router.navigateByUrl("/auth/update-phone");
          } else {
            this.router.navigateByUrl("/auth/verify");
          }
        } else {
          this.store.dispatch(authLogin());
          this.router.navigateByUrl("/home");
        }
      },
      (onError: any) => {
        console.log(onError);
      }
    );
  }

  signOut(): void {
    this.authService.signOut();
  }

  forgotPassword() {
    console.log('Sent forgot password link to your email');
  }

}
