import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../shared/shared.module';

import { NgxIntlTelInputModule } from 'ngx-intl-tel-input';
import { SocialLoginModule, AuthServiceConfig } from "angularx-social-login";
import { GoogleLoginProvider, FacebookLoginProvider } from "angularx-social-login";

import { AuthRoutingModule } from './auth-routing.module';
import { LoginComponent } from './login/login.component';
import { SignupComponent } from './signup/signup.component';
import { UpdatePhoneComponent } from './update-phone/update-phone.component';
import { VerifyComponent } from './verify/verify.component';
import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';

const config = new AuthServiceConfig([
  {
    id: GoogleLoginProvider.PROVIDER_ID,
    provider: new GoogleLoginProvider("242880965663-qn8m5e9kfi2s88iegb6kbjmo7rna9ren.apps.googleusercontent.com")
  },
  {
    id: FacebookLoginProvider.PROVIDER_ID,
    provider: new FacebookLoginProvider("483715322266021")
  }
]);

export function provideConfig() {
  return config;
}


@NgModule({
  declarations: [LoginComponent, SignupComponent, UpdatePhoneComponent, VerifyComponent, ForgotPasswordComponent],
  imports: [
    CommonModule,

    NgxIntlTelInputModule,
    NgxIntlTelInputModule,
    SocialLoginModule,
    AuthRoutingModule,
    SharedModule,
  ],
  providers: [
    {
      provide: AuthServiceConfig,
      useFactory: provideConfig
    }
  ]
})
export class AuthModule { }
